@import './Vars.scss';

.info{
  .container{
    display: flex;
    justify-content: space-between;
    padding: 0 38px;
  }

  @media (max-width: 767px) {
    flex-direction: column;
    justify-content: start; 
  }
  &__left-block,
  &__right-block{
    width: 33.333%;
    max-width: 330px;
    
    @media (max-width: 767px) {
      width: 100%;
      max-width: none;
    }
  }
  &__contact{
    * {
      margin-bottom: 20px;
    }
  }
  &__about{}
  &__clients{}
  &__features{}
  &__title{
    font-family: "Crimson Text";
    color: $grey;
    font-size: 28px;
  }
  &__text{
    font-family: "Lato";
    color: $grey;
  }
  &__link{
    font-family: "Lato";
    color: $lightbrown;
    font-size: 14px;
    
    &:hover{
      color: $lightbrown;
      text-decoration: underline;
    }
  }
  &__list{
    line-height: 140%;
  }
  &__item{

  }
}