@import './Vars.scss';

.main{
  display: flex;
  flex-direction: column;
  align-items: center;

  &__controls{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 40px;
  }
  &__bottom-block{
    width: 100%;
  }
  &__button{
    height: 40px;
    width: 80px;
    font-size: 14px;
    border: 0;
    border-top: 1px solid $grey;
    color: $grey;
    background: none; 
    font-family: "Lato";
    
    transition: color 0.3s, border 0.3s;
    &:hover{
      color: $darkgrey;
      border-top: 1px solid $darkgrey;
      cursor: pointer;
    }
  }
  // &__left-button{
    
    // }
    // &__right-button{
      
      // }

}
