@import "./Vars.scss";

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  position: relative;
  background: $white;
  width: 100%;
}
a{
  text-decoration: none;
  &:hover{
  }
  &:visited{
  }
}

.container{
  width: calc(100% - 40px);
  max-width: 1080px;
  margin: auto;
}

.masonry-block{
  padding-bottom: 20px;

  &__item{
    width: 100%;
    height: 100%;
    display: block;
  }
}

  
.selected{
  display: flex;
  // width: 100%;
  margin-bottom: 26px;
  justify-content: center;

  @media (max-width: 767px) {
    flex-direction: column;
    align-items: center;
  }
  &__image-wrapper{
    // width: 100%;
  }
  &__image{
    height: 100%; 
    width: 100%;
    object-fit: cover;
    height: min(calc(90vh - 155px), 100vw);
  }
  &__title{
    max-width: 300px;
    align-self: flex-end;
    padding: 0 20px;
    color: $grey;
    font-size: 27px;
    font-family: "Lato";
    font-weight: 400;
    @media (max-width: 767px) {
      align-self: center;
    }
  }
}