@import 'Vars.scss';

.portraits{
  display: flex;
  flex-direction: column;
  align-items: center;

  &__item-block{
    width: 100%;
  }
  &__item{
    &:visited{
      color: inherit;
    }
  }
  &__image{
    width: 100%;
    height: 100%;
    display: block;
  }
  &__title{
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: "Jost";
    font-size: 30px;
    color: $grey;
  }
}